// Colors
// ---------------------------------

// Default

$color-default-black:               #000000;
$color-default-white:               #ffffff;
$color-transparent:                 rgba(255, 255, 255, 0);

// Project palette
// для нейминга цветов используем https://www.htmlcsscolor.com/hex/334482

$color-neon-blue:                   #2c39f2;
$color-torch-red:                   #ff1553;

$color-alice-blue:                  #f2f5f6;
$color-dark-white:                  rgba(255, 255, 255, 0.7);
$color-deep-sky-blue:               #00b2ff;
$color-deep-sky-blue-light:         rgba(0, 178, 255, 0.5);
$color-black-pearl:                 #041827;
$color-black-pearl-zero:            rgba(4, 24, 39, 0);
$color-black-pearl-lighter:         rgba(4, 24, 39, 0.5);
$color-black-pearl-lightest:        rgba(4, 20, 39, 0.15);
$color-box-shadow:                  rgba(0, 0, 0, 0.25);
$color-silver:                      #c4c4c4;
$color-alice-blue-lighter:          rgba(248, 253, 255, 0.7);
$color-tory-blue:                   #f8fdff;
$color-whisper:                     #ebebeb;

// Gradient

$black-to-right:                    linear-gradient(90deg, rgba(0, 0, 0, 0.75) 0%, rgba(0, 0, 0, 0) 75%);
$black-pearl-to-bottom:             linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%);

// Typography
// ---------------------------------

// переменная используемая в html для подключения скейлинга
$fz-default:                        16px;

// у некоторых шрифтов в ios возникают проблемы с символом рубля
// https://www.artlebedev.ru/kovodstvo/sections/159/#13
$ff-rouble:                         "Rouble", "Arial", sans-serif;

$ff-placeholder:                    "Roboto-Condensed", "Arial", sans-serif;

$ff-addition:                       "PT Mono", monospace;

// Animation
// ---------------------------------

$tf-default:                        ease;
$trans-default:                     0.3s $tf-default;

// если требуется изинг отличный от $tf-default, то переменную называем не цифрой
$trans-modal:                       0.6s cubic-bezier(0.55, 0, 0.1, 1);

$trans-600:                         0.6s $tf-default;

// Viewports
// ---------------------------------

$vp-320:                            320px;
$vp-375:                            375px;
$vp-768:                            768px;
$vp-1024:                           1024px;
$vp-1280:                           1280px;
$vp-1440:                           1440px;
$vp-1920:                           1920px;

// Retina
// ---------------------------------

$retina-dpi:                        144dpi;
$retina-dppx:                       1.5dppx;
